.container{
    height: 100vh;
    display: grid;
    grid-template-rows: var(--my-nav-bar-pc-height) 1fr 4em;
    overflow: hidden;
}

.mainContent{
    display: grid;
    grid-template-columns: 2fr 5fr;
    background-color: white;
    height: 100%;
}

.infoPart{
    background-color: var(--darker-field);
    padding-left: 5%;
}

.playlistCover{
    margin-top: 5%;
    height: 19vw;
    width: 19vw;
    max-width: 25em;
    max-height: 25em;
}

.playlistInfo{
    display: grid;
    grid-template-columns: 1fr 2em;
    grid-auto-flow: column;
    grid-template-rows: auto auto auto;
    text-align: center;
    width: max-content;
    height: max-content;
    margin: 2em auto;
}

.playlistTitle{
    font-size: min(2vw, 2.6em);
    color: var(--dark-text);
    margin: 0.1em;
}

.theCreatorsName{
    font-size: min(1.6vw, 1.7em);
    margin: 0.05em;
    color: var(--bright-text)
}

.songsLength{
    margin: 0.7em;
    font-size: min(1.5vw, 1.6em);
}

.more{
    grid-row: 1;
    grid-column: 2;
    align-self: center;
    object-fit: cover;
    height: 70%;
    width: 80%;
    margin-left: 1em;
}

.songsPart{
    width: 100%;
    display: flex;
    height: 100%;
}

.songList{
    align-self: flex-end;
    width: 100%;
}